.inputContainer {
    display: flex;
    justify-content: center;
    flex-grow: 1;
    margin-top: 15px;
    font-size: 1.1rem;
    margin-bottom: 15px;
    height: 44px;
}

input,
input:focus,
input:not(:focus) {
    border: 0;
    border-bottom: 1px solid #b1b1b1;
    font-family: "NouvelR-Regular", sans-serif;
    outline: 0;
    color: #000;
    padding: 7px;
    background: transparent;
    transition: .3s;
    flex-shrink: 1;
    flex-grow: 1;
}

input:focus {
    /* border-color: #5f5e5e; */
    transition: .7s;
    font-family: "NouvelR-Regular", sans-serif;
    /* color: #3a3a3a; */
}

input::placeholder {
    color: #817f7f;
    font-family: "NouvelR-Light", sans-serif;
}

.rightBlock {
    font-family: "NouvelR-Light", sans-serif;
    margin-left: 15px;
    border: none;
    display: flex;
    flex-grow: 1;
    max-width: 350px;
}

.label {
    text-align: right;
    flex-grow: 1;
}

