    flex-basis: 350px;
    max-width: 350px;
    border-radius: 0;
    border: 1px solid #d9d9d6;
    outline: none;
    font-size: 1.1rem;
} */

input {
    flex-basis: 350px;
    max-width: 350px;
    font-family: inherit;
    border-radius: 0;
    outline: none;
    font-size: 1.1rem;
}

.label {
    display: inline-block;
    flex-basis: 150px;
    max-width: 150px;
    vertical-align: bottom;
    align-self: center;
    word-wrap: break-word;
}

@media screen and (max-width:560px) {
    .inputContainer {
        flex-wrap: wrap;
        margin-top: 20px;
        margin-bottom: 22px;
        height: auto;
        /* row-gap: 5px; */
    }

    .rightBlock {
        margin-left: 0;
    }

    .label {
        text-align: left;
        max-width: 350px;
        flex-basis: 200px;
        margin-bottom: 5px;
    }

    input {
        width: 300px;
        max-width: 100%;
    }

    input,
    input:focus,
    input:not(:focus) {
        padding-left: 0;
    }
}

@media screen and (max-width:433px) {
    input {
        width: auto;
    }
}