.buttonsContainer {
    z-index: 2;
    margin-top: 20px;
    width: 100%;
    display: flex;
    justify-content: center;
    /* column-gap: 10px; */
}

.button {
    font-family: inherit;
    height: 30px;
    width: 30px;
    border-radius: 0;
    /*color: #656666*/
    outline: none;
    font-size: 1.4rem;
    background-color: rgb(255, 218, 68);
    border-radius: 4px;
    border: none;
    transition: .4s;
    display: flex;
    justify-content: center;
    align-items: center;
}

.button:last-child {
    margin-left: 10px;
}


.button:hover {
    cursor: pointer;
    background-color: rgb(253, 240, 120);
}

.button>div {
    width: 100%;
}